import React from 'react';
import ReactDOM from 'react-dom';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';

import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';


import jsondata from './questionbank.json';
import 'bootstrap/dist/css/bootstrap.min.css';

import './index.css';




function Instruction(props){
	if (props.display===0){
		return(
		
		<div className="mx-auto">
			Please read the following instructions carefully before you start:
			<br/><br/>
			<strong> Instruction</strong>
			<br/><br/>
			<ListGroup>
  <ListGroup.Item>Please do not click "go back" in your browser.</ListGroup.Item>
  <ListGroup.Item>Please do not close this page until you have completed the exercise.</ListGroup.Item>
  
  <ListGroup.Item>You cannot modify your answer after clicking "submit" button.</ListGroup.Item>
  <ListGroup.Item>The author may have helpful comments for each question. Be sure to check for this feedback.
</ListGroup.Item>
</ListGroup>
			<br/><br/>
			Good luck!
			<br/><br/>
			Press Start button to start your quiz.
			<br/><br/>
			
			
		</div>
		);
	}else return("");
}

 function Printquestion(props){

	if (props.questionnow>0 && props.questionnow<=jsondata.length){
	return(
		<div>{jsondata[props.questionnow-1].questiontext}</div>
	);
	}else return("");

}

function Printimg(props){
	
	
	return  (<div><img src={props.src} alt={jsondata.questionnumber}/></div> );

}

function Printans(props){
	
		return(
		 <Button  onClick={()=>props.onClick()} variant={props.color} block disabled={props.disabled}>
    {jsondata[props.questionnow-1].answer[props.i].ans}
  </Button>
	

		);
	}
	
function Printfeedback(props){
	return(
	<div>{jsondata[props.questionnow-1].answer[props.history[props.questionnow-1]].feedback}</div>
	);

}

function Printreview(props){
	
	return(
	<div><h4>Well Done, you have finished the quiz!</h4> <br/> Go review the questions</div>
	);
	
	
}
	
	

function Controlbutton(props){
	
	return(
	<Button className="float-right" onClick={() => props.onClick()} disabled={props.disabled}>
		{props.value}
		</Button>
	);
}

function Calculation(props){
	let score=0;
	let x;
	let i=0;
	
	for (x of props.history)
		{
			if (jsondata[i].answer[x].fraction=== "1")
				score+=1/ jsondata.length;
			i++;

		}
	return ( <div><br/> Your score is {score*100}</div> );
}



class Problem extends React.Component{
		constructor(props){
		super(props);
		this.state={
			questionnow:0,
			answered:false,
			history:Array(jsondata.length).fill(null),
			review:false,
			};
	}
	
	renderimg=()=>{
		if (this.state.questionnow>0 && this.state.questionnow<=jsondata.length && jsondata[this.state.questionnow-1].image!==""){
			
			const img = jsondata[this.state.questionnow-1].image;
			
			return <Printimg questionnow={this.state.questionnow} src={img} />;
			
		}
	}
	
	renderans=(i)=>{
		let color="light";
		if (this.state.questionnow>0 && this.state.questionnow<=jsondata.length){
			if (jsondata[this.state.questionnow-1].answer[i].fraction==="1" && this.state.answered===true)
				color="success"
			if (this.state.history[this.state.questionnow-1]===i && jsondata[this.state.questionnow-1].answer[i].fraction==="0" && this.state.answered===true)
				color="danger"
				
			
		return <Printans color={color} questionnow={this.state.questionnow} answered={this.state.answered} onClick={()=>this.anshandle(i)} i={i} disabled={this.state.answered ? "1" : ""}/>;
		} else return;
	
	}
	
	renderfeedback=()=>{
		if (this.state.answered===true && this.state.questionnow>0 && this.state.questionnow<=jsondata.length)
			return <Printfeedback questionnow={this.state.questionnow} history={this.state.history} />;
		
	}
	
	renderreview=()=>{
		if(this.state.questionnow===jsondata.length+1){

	
			return (<div><Printreview />
					<Calculation history={this.state.history} />
					</div>
					);}
		
		
	}
	
	
	renderbutton=()=>{
				let buttonname="";
			let disabled="";
		if (this.state.questionnow===0 ){
			buttonname="Start";
			disabled="";
		}else{
			buttonname="Next";
			if(this.state.answered===true || this.state.review===true)
				disabled="";
			else disabled="1";
		}
		return <Controlbutton value={buttonname} onClick={()=>this.nextbutton()} disabled={disabled}/>;
		
	}
	
	
	
	anshandle=(ansnumber)=>{
		const history=this.state.history.slice();
		
	
		
		if (this.state.questionnow>0 && this.state.questionnow<=jsondata.length)
			{history[this.state.questionnow-1]=ansnumber;
			this.setState({
			history:history,
			answered:true,
		});	}
		
	
	}
	
	nextbutton=()=>{
		let now = this.state.questionnow+1;
		
		if (now>jsondata.length)
					this.setState({
				review:true,
						
			});
		if (now>jsondata.length+1)
			now=1;
		
		if (this.state.review===true){
			this.setState({
			questionnow:now,
			answered:true,
		});	
		}else{
		
		this.setState({
			questionnow:now,
			answered:false,
		});	};
	}
	
	
	render(){

		
		return(
			<div>
			<Instruction display={this.state.questionnow} />
			<Printquestion questionnow={this.state.questionnow} />
			{this.renderimg()}
			{this.renderans(0)}
			{this.renderans(1)}
			{this.renderans(2)}
			{this.renderans(3)}
			{this.renderfeedback()}
			{this.renderreview()}
			{this.renderbutton()}
			</div>
		);
	}
}


class MC extends React.Component{
	constructor(props){
		super(props);
		this.state={
			
			
			};
	}

	render(){

		
		return(
			
	<Container>
		<h1>Self-learning exercise</h1>
		 <Card body >
			
		<Problem />        

			</Card>
		</Container>
	
		);
	}
}


ReactDOM.render(
  <MC />,
  document.getElementById('root')
);

